import React from "react"

import Table from 'react-bootstrap/Table';
import { Heading } from "../../../components/heading";

import "./index.css";

const Date = () => {
  return (
    <div>
      <Heading as="h2" >日時・場所</Heading>
      <Table bordered hover>
        <thead>
          <tr>
            <th>日時</th>
            <th>場所</th>
            <th>イベント</th>
          </tr>
        </thead>
        <tbody>
          <tr>
            <td>4月4日(木)~6日(土)</td>
            <td>メインストリート</td>
            <td>チラシ配布</td>
          </tr>
          <tr>
            <td>4月14日(日)</td>
            <td>B棟</td>
            <td>
              <ul className="mb-0">
                展示会
                <li>11:00~ 合同説明会</li>
                <li>13:00~ LT会</li>
              </ul>
            </td>
          </tr>
        </tbody>
      </Table>
      <br />
      <Heading as="h2" >詳細</Heading>
      <ul>
        <li>
          4月14日の展示会では、それぞれのサークルごとのスペースがあるので、興味のあるサークルの活動内容を知ることができます！
        </li>
        <li>
          合同説明会では、各サークルから3~5分程度サークルの魅力を発表します！
        </li>
        <li>
          LT会では、サークル同士の交流や活動内容の紹介を行います！
        </li>
      </ul>
    </div>
  )
}

export default Date